import {
  ACTION_EVENTS,
  add,
  alertManager,
  AlertType,
  decimalFormatter,
  findById,
  formatRef,
  isEmpty,
  parseLastDay,
  parseRefToDate,
  progressActions,
  toDate,
} from 'summer';
import { CLASS_NAME } from '../models/dtos/mfp/vinculo';

export const condicional = (condicao, conteudo) => (condicao ? conteudo : null);
export const getReferenciaAtual = globalReducer => globalReducer.globalParameter.empresaMFP.referencia;

export const showLoading = () =>
  progressActions.showProgress({
    animated: true,
    className: 'progress-bar-rounded progress-animated-alt',
    color: 'first',
    striped: true,
    value: 100,
  });

export const promisedAlert = (message, type = AlertType.WARNING_YES_NO) =>
  new Promise(resolve => {
    promisedAlertWithCatch(message, type)
      .then(resolve)
      .catch(() => null);
  });

export const promisedAlertWithCatch = (message, type = AlertType.WARNING_YES_NO) =>
  new Promise((resolve, reject) => {
    alertManager.emit(ACTION_EVENTS.SHOW, {
      message,
      onCancelClick: () => reject(),
      onNoClick: () => reject(),
      onOkClick: () => resolve(),
      type,
    });
  });

export const getCargoAtualReferencia = list => {
  let result = '';

  if (list.length > 0) {
    const atual = list.reduce((prev, current) => (toDate(prev.dataAlteracao) > toDate(current.dataAlteracao) ? prev : current));
    if (atual) {
      result = atual.cargo.descricao;
    }
  }

  return result;
};

export const getCargoReferencia = (values, referencia) => {
  let result = values.cbo ? values.cbo.descricao : '';

  if (referencia) {
    const refDate = parseLastDay(referencia);
    const list = values.cargos.filter((item: any) => toDate(item.dataAlteracao) <= refDate);
    result = getCargoAtualReferencia(list);
  }

  return result;
};

export const getItemAtual = list => {
  let result = null;

  if (list.length > 0) {
    result = list.reduce((prev, current) => (prev.nroOrdem > current.nroOrdem ? prev : current));
  }

  return result;
};

export const getSalarioReferencia = (values, referencia) => {
  let result = null;

  if (values.salarios.length > 0) {
    if (referencia) {
      const refDate = parseLastDay(referencia);
      const list = values.salarios.filter((item: any) => toDate(item.data) <= refDate);

      if (list.length > 0) {
        result = getItemAtual(list);
      }
    } else {
      result = getItemAtual(values.salarios);
    }
  }

  return result;
};

export const isSalarioRepetido = (salarioA, salarioB) =>
  ['tipoPagamento', 'horista', 'numeroHorasContratuais', 'numeroHorasContratuaisFixa', 'valor'].every(
    column => salarioA[column] === salarioB[column]
  ) && compararOutrasRemuneracoes(salarioA.outrasRemuneracoes, salarioB.outrasRemuneracoes);

export const isOutraRemuneracaoRepetido = (outraRemuneracaoA, outraRemuneracaoB) =>
  ['valor', 'insideFgts', 'insideInss', 'insideIrrf', 'cpf', 'cnpj'].every(column => outraRemuneracaoA[column] === outraRemuneracaoB[column]);

export const compararOutrasRemuneracoes = (outrasRemuneracoesA, outrasRemuneracoesB) =>
  outrasRemuneracoesA.length === outrasRemuneracoesB.length &&
  outrasRemuneracoesA.every((outraRemuneracaoA, index) => isOutraRemuneracaoRepetido(outraRemuneracaoA, outrasRemuneracoesB[index]));

export const isVinculoProfessor = vinculo => vinculo.cbo && vinculo.cbo.professor;

export const getIntervaloReferencia = (refInicio, refFinal) => {
  const referencias = [refInicio];

  if (refInicio !== refFinal) {
    let refAtual = refInicio;
    let dataAtual = parseRefToDate(refInicio, 1);

    while (refAtual !== refFinal) {
      dataAtual = add(dataAtual, 1, 'month');
      refAtual = formatRef(dataAtual);
      referencias.push(refAtual);
    }
  }

  return referencias;
};

export const getMaxRef = lista => {
  const item = lista.reduce((prev, curr) => (parseInt(prev.referencia, 10) < parseInt(curr.referencia, 10) ? curr : prev));
  return item.referencia;
};

export const getMinRef = lista => {
  const item = lista.reduce((prev, curr) => (parseInt(prev.referencia, 10) > parseInt(curr.referencia, 10) ? curr : prev));
  return item.referencia;
};

export const getNextRef = ref => formatRef(add(parseRefToDate(ref, 1), 1, 'month'));

export const valorTotalOutrasRemuneracoes = lista => {
  let result = 0;

  if (lista.length > 0) {
    result = lista.length === 1 ? lista[0].valor : lista.map(item => item.valor).reduce((total, item) => total + item);
  }

  return decimalFormatter(result, null);
};

export const numHorasFormatter = cell => {
  const value = decimalFormatter(cell, null);
  const result = parseFloat(value.replace(',', '.'));

  let hours = parseInt(result.toString(), 10);
  let minutes = (result - hours) * 60;

  minutes = Math.abs(Math.round(minutes));

  if (minutes === 60) {
    minutes = 0;
    hours += 1;
  }

  const formatado = hours.toFixed(0).padStart(3, '0') + ':' + minutes.toFixed(0).padStart(2, '0') + 'h';

  return value + ' - ' + formatado;
};

export const registrarCampos = (formProps, campos: any[]) => campos.forEach(name => formProps.register({ name }));

export const getTodasListasVinculo = (ignorar: string[] = []) => {
  const listaCompleta = [
    'cargos',
    'diferenciaisAcordoConvencaoDissidio',
    'examesOcupacionais',
    'ferias',
    'feriasRescisao',
    'movimentacoes',
    'planos',
    'previdenciasComplementar',
    'quadrosHorarios',
    'reintegracoes',
    'salarios',
    'tomadoresServico',
    'trabalhosIntermitentes',
    'valesTransporte',
  ];

  let result = listaCompleta;

  if (ignorar.length > 0) {
    result = listaCompleta.filter(item => ignorar.indexOf(item) === -1);
  }

  return result;
};

export const registrarListasVinculo = (formProps, ignorar: string[] = [], camposExtras: string[] = []) =>
  registrarCampos(formProps, [...getTodasListasVinculo(ignorar), ...camposExtras]);

export const loadVinculo = (formProps, dispatch, onLoad: (rawData?: any) => void = null, mapKey = null) => {
  const values = formProps.getValues();
  if (!isEmpty(values.vinculo)) {
    dispatch(showLoading());
    findById(values.vinculo, CLASS_NAME, {
      thenFunction: res => {
        const data = mapKey ? res[mapKey] : res;
        dispatch(progressActions.hideProgress());
        formProps.setValue(Object.keys(data).map(key => ({ [key]: data[key] })));
        if (onLoad) {
          onLoad(res);
        }
      },
    });
  }
};

export const getFullVinculo = (formProps, selectedItem, callback) => {
  const values = formProps.getValues();
  const id = selectedItem ? selectedItem.id : values.vinculo.id;
  const parentData = selectedItem || {};

  const formKeys = Object.keys(values);
  const parentKeys = Object.keys(parentData);

  findById({ id }, CLASS_NAME, {
    thenFunction: res => {
      const keysResponse = Object.keys(res);

      parentKeys.forEach(key => {
        if (keysResponse.indexOf(key) !== -1) {
          res[key] = parentData[key];
        }
      });

      formKeys.forEach(key => {
        if (keysResponse.indexOf(key) !== -1) {
          res[key] = values[key];
        }
      });

      callback(res);
    },
  });
};
