import { BasicEnum } from '../models/enumerated/basicEnum';

interface EnumWithExtraFields extends BasicEnum {
  extraFields?: any;
}

const EnumUtil = {
  contains: (list: EnumWithExtraFields[], search: EnumWithExtraFields | string, extraField: string = null) =>
    EnumUtil.find(list, search, extraField) !== undefined,
  find: (list: EnumWithExtraFields[], search: EnumWithExtraFields | string, extraField: string = null) => {
    let result;

    if (search) {
      result = list.find(item => (extraField ? item.extraFields[extraField] : item.value) === (typeof search === 'object' ? search.value : search));
    }

    return result;
  },
};

export default EnumUtil;
