import React, { FC, useEffect, useState } from 'react';
import { useForm } from 'react-hook-form';
import { Col, Row } from 'reactstrap';
import { AlertType, Checkbox, compare, DetailEditForm, DetailEditViewFormProps, InputGroupInline, InputType } from 'summer';
import { FeriasRescisao } from '../../../../models/dtos/mfp/feriasRescisao';
import { promisedAlert } from '../../../../utilities/vinculo.util';

const FeriasRescisaoEditView: FC<DetailEditViewFormProps<FeriasRescisao>> = props => {
  const formProps = useForm<FeriasRescisao>({ defaultValues: props.selectedItem || {}, mode: 'onBlur' });

  const [valorDisabled, setValorDisabled] = useState(true);

  useEffect(
    () => {
      if (props.selectedItem) {
        setValorDisabled(!props.selectedItem.valorManual);
      }
      if (!props.selectedItem || !props.selectedItem.nroFaltas) {
        formProps.setValue('nroFaltas', 0);
      }
    },
    // eslint-disable-next-line react-hooks/exhaustive-deps
    []
  );

  const changeValorManual = () => {
    const values = formProps.getValues();
    setValorDisabled(!values.valorManual);
  };

  const validateExtras = (formData, _, parentData) => {
    const erros = [];

    if (compare(formData.dtInicialPerAquisitivo, formData.dtFinalPerAquisitivo) > 0) {
      erros.push('O Período aquisitivo inicial não pode ser maior ou igual ao Período aquisitivo final');
    } else {
      if (parentData.rescisaoJaCalculada && formData.valorManual) {
        promisedAlert('A rescisão deverá ser recalculada para que os valores sejam atualizados.', AlertType.WARNING);
      }
    }

    return erros;
  };

  return (
    <DetailEditForm handleSubmit={props.handleSubmit} handleCancel={props.handleCancel} formProps={formProps} validateExtras={validateExtras}>
      <Row>
        <Col>
          <InputGroupInline
            id="dtInicialPerAquisitivo"
            label="Período Aquisitivo"
            formProps={{ ...formProps, validation: { require: true } }}
            isPrependLabel={true}
            type={InputType.DATE}
          />
        </Col>
      </Row>
      <Row>
        <Col>
          <InputGroupInline
            id="dtFinalPerAquisitivo"
            label="Até"
            formProps={{ ...formProps, validation: { require: true } }}
            isPrependLabel={true}
            type={InputType.DATE}
          />
        </Col>
      </Row>
      <div className="d-flex">
        <div className="flex-grow-1">
          <InputGroupInline
            id="nroFaltas"
            label="Nº de Faltas"
            formProps={{ ...formProps, validation: { require: true } }}
            isPrependLabel={true}
            type={InputType.NUMBER}
          />
        </div>
        <div style={{ width: 200, marginLeft: 10, marginTop: 10 }}>
          <Checkbox id="reflexoDobra" formProps={formProps} label="Reflexo de Dobra" />
        </div>
      </div>
      <div className="d-flex">
        <div className="flex-grow-1">
          <InputGroupInline id="valor" label="Valor" formProps={formProps} isPrependLabel={true} disabled={valorDisabled} />
        </div>
        <div style={{ width: 194, marginLeft: 10, marginTop: 10 }}>
          <Checkbox id="valorManual" formProps={formProps} label="Valor Manual" onChange={changeValorManual} />
        </div>
      </div>
    </DetailEditForm>
  );
};

export default FeriasRescisaoEditView;
