import { saveAs } from 'file-saver';
import React, { FC, useEffect, useRef, useState } from 'react';
import { useForm } from 'react-hook-form';
import { useDispatch } from 'react-redux';
import { RouteComponentProps } from 'react-router';
import { Button, Col, Row } from 'reactstrap';
import {
  AlertType,
  boolColumn,
  Box,
  Checkbox,
  compare,
  decimalFormatter,
  DefaultSortProps,
  DetailManagerForm,
  DetailManagerFormProps,
  EditForm,
  EditFormRefProps,
  EditViewProps,
  ErroList,
  getSelectedItem,
  InputGroupInline,
  InputType,
  isEmpty,
  onlyNumber,
  OrderType,
  ProcessBar,
  progressActions,
  WikiButton,
  WindowComponent,
  Wizard,
  WizardActionFinishProps,
  WizardPage,
} from 'summer';
import { Rescisao } from '../../../../models/dtos/mfp/rescisao';
import { CLASS_NAME, Vinculo } from '../../../../models/dtos/mfp/vinculo';
import {
  DEMISSAO_ENUM_VALUES,
  DESPEDIDA_SEM_JUSTA_CAUSA,
  MUDANCA_REGIME_JURIDICO,
  RESCISAO_FALECIMENTO_EMPREGADO,
  SEM_COMPATIBILIDADE_HOMOLOGNET_ENUM_VALUES,
} from '../../../../models/enumerated/mfp/demissaoEnum';
import { INDICADOR_CUMPRIMENTO_AV_PREVIO_ENUM_VALUES } from '../../../../models/enumerated/mfp/indicadorCumprimentoAvPrevioEnum';
import { TIPO_AVISO_PREVIO_ENUM_VALUES } from '../../../../models/enumerated/mfp/tipoAvisoPrevioEnum';
import { apagarRescisao, definirAvisoPrevio, obterFeriasRescisao, rescisaoCalculada, simularRescisao } from '../../../../services/mfp.service';
import { fileDownload } from '../../../../services/report.service';
import EnumUtil from '../../../../utilities/enum.util';
import { condicional, getFullVinculo, loadVinculo, promisedAlert, promisedAlertWithCatch, showLoading } from '../../../../utilities/vinculo.util';
import VinculoIS from '../../../components/mfp/vinculoIS/VinculoIS';
import FeriasRescisaoEditView from './FeriasRescisaoEditView';

interface RescisaoEditViewProps extends EditViewProps<Rescisao> {
  onDockSubmit?: (data: any) => void;
  openViaDock?: boolean;
}

interface ScreenFields {
  vinculo?: Vinculo;
  rescisaoJaCalculada?: boolean;
}

const RescisaoEditView: FC<RescisaoEditViewProps & RouteComponentProps> = props => {
  const selectedItem = getSelectedItem(props);
  const formProps = useForm<Rescisao & ScreenFields>({
    defaultValues: { ...(selectedItem && selectedItem.recisao ? selectedItem.recisao : {}), rescisaoJaCalculada: false },
    mode: 'onBlur',
  });

  const dispatch = useDispatch();
  const editFormRef = useRef<EditFormRefProps>();

  const [isSimularVisivel, setIsSimularVisivel] = useState(true);
  const [disabledCumprimentoAviso, setDisabledCumprimentoAviso] = useState(false);
  const [exibirTipoAviso, setExibirTipoAviso] = useState(false);
  const [fullVinculo, setFullVinculo] = useState(null);
  const [falecimentoEmpregado, setFalecimentoEmpregado] = useState(true);
  const [mudancaRegimeJuridico, setMudancaRegimeJuridico] = useState(true);

  useEffect(
    () => {
      setTimeout(() => {
        formProps.register({ name: 'rescisaoJaCalculada' });
        if (selectedItem && !isEmpty(selectedItem.id)) {
          getFullVinculo(formProps, props.selectedItem, vinculo => {
            setFullVinculo(vinculo);
            checkRescisaoCalculada();
          });
        } else {
          if (!selectedItem || isEmpty(selectedItem.id)) {
            setIsSimularVisivel(false);
          }
        }
      }, 700);
    },
    // eslint-disable-next-line react-hooks/exhaustive-deps
    []
  );

  const onPageChange = currentPageId => {
    if (currentPageId === 'rescisao') {
      loadVinculo(
        formProps,
        dispatch,
        vinculo => {
          setFullVinculo(vinculo);
          if (vinculo.recisao && vinculo.recisao.id) {
            checkRescisaoCalculada();
          }
        },
        'recisao'
      );
    }
  };

  const checkRescisaoCalculada = () => {
    rescisaoCalculada(
      { ...formProps.getValues(), vinculo: fullVinculo },
      {
        thenFunction: res => formProps.setValue('rescisaoJaCalculada', res),
      }
    );
  };

  const reset = () => {
    formProps.setValue('vinculo', null);
    setFullVinculo(null);
  };

  const onCancel = () => {
    editFormRef.current.close();
  };

  const handleSubmitViaDock = () => {
    editFormRef.current.submit(false).then(res => {
      editFormRef.current.close();
      if (props.onDockSubmit) {
        props.onDockSubmit(res);
      }
    });
  };

  const changeAvisoDispensaIndenizado = () => {
    const values = formProps.getValues();
    const is = values.avisoPrevioIndenizado || values.dispensadoAvisoPrevio;
    setDisabledCumprimentoAviso(!is);
    setExibirTipoAviso(is);
    if (is) {
      formProps.setValue([{ indicadorCumprimentoAvPrevio: null }, { tipoAvisoPrevio: null }]);
    }
  };

  const getFeriasRescisao = data => {
    if (data.dtAfastamento) {
      promisedAlert('Deseja obter as férias que deverão ser incluídas na Rescisão?').then(() => {
        obterFeriasRescisao(
          { ...formProps.getValues(), vinculo: fullVinculo },
          {
            thenFunction: res => {
              formProps.setValue('feriasRescisao', res);
            },
          }
        );
      });
    } else {
      promisedAlert('É necessário definir a Data de afastamento.', AlertType.WARNING);
    }
  };

  const dataAfastamentoBlur = () => {
    const values = formProps.getValues();
    if (values.dtAfastamento && fullVinculo.dataAdmissao) {
      promisedAlertWithCatch('Deseja definir os Dias e a Data de aviso prévio com base nas informações do Trabalhador?')
        .then(() => {
          definirAvisoPrevio(
            { ...formProps.getValues(), vinculo: fullVinculo },
            {
              thenFunction: res => {
                delete res.vinculo;
                formProps.setValue(Object.keys(res).map(key => ({ [key]: res[key] })));
                getFeriasRescisao(res);
              },
            }
          );
        })
        .catch(() => {
          getFeriasRescisao(values);
        });
    }
  };

  const motivoChange = value => {
    const values = formProps.getValues();

    const isFalecimentoEmpregado = value !== RESCISAO_FALECIMENTO_EMPREGADO.value;
    const isMudancaRegimeJuridico = value !== MUDANCA_REGIME_JURIDICO.value;

    setFalecimentoEmpregado(isFalecimentoEmpregado);
    setMudancaRegimeJuridico(isMudancaRegimeJuridico);

    formProps.setValue([
      { nrCertidaoObito: isFalecimentoEmpregado ? null : values.nrCertidaoObito },
      { cnpjEmpresaSucessora: isMudancaRegimeJuridico ? null : values.cnpjEmpresaSucessora },
    ]);
  };

  const motivoBlur = () => {
    const values = formProps.getValues();
    if (values.tipoDemissao === DESPEDIDA_SEM_JUSTA_CAUSA.value) {
      formProps.setValue('arrecadouFgtsGuiaRecolhimentoRescisorio', true);
    }
  };

  const validateExtras = (formData, rescisaoObrigatoria = false) => {
    const erros = [];

    const is = isEmpty(formData.dtAfastamento) && isEmpty(formData.dtAvisoPrevio) && isEmpty(formData.tipoDemissao);

    if (rescisaoObrigatoria && is) {
      erros.push('É necessário preencher a rescisão');
    } else if (!isEmpty(formData.dtAfastamento) && compare(formData.dtPagamento, fullVinculo.dataAdmissao) === -1) {
      erros.push('Data do Afastamento da Rescisão não pode ser anterior a Data de Admissão');
    } else if (!isEmpty(formData.dtPagamento) && compare(formData.dtPagamento, fullVinculo.dataAdmissao) === -1) {
      erros.push('Data do Pagamento da Rescisão não pode ser anterior a Data de Admissão');
    } else {
      if (formData.dtAfastamento || formData.dtAvisoPrevio || formData.tipoDemissao) {
        if (formData.avisoPrevioIndenizado && isEmpty(formData.dtAvisoPrevio)) {
          erros.push('Data do aviso prévio não pode estar em branco.');
        }

        if (isEmpty(formData.dtAfastamento)) {
          erros.push('Data de afastamento não pode estar em branco.');
        }

        if (isEmpty(formData.dtPagamento)) {
          erros.push('Data do pagamento não pode estar em branco.');
        }

        if (formData.tipoDemissao) {
          if (formData.tipoDemissao === MUDANCA_REGIME_JURIDICO.value && isEmpty(formData.cnpjEmpresaSucessora)) {
            erros.push('O CNPJ da Empresa Sucessora é obrigatório para o tipo de demissão ' + MUDANCA_REGIME_JURIDICO.label);
          }
        } else {
          erros.push('Motivo da rescisão não pode estar em branco.');
        }
      }

      if (formData.dtAfastamento && formData.dtAvisoPrevio && compare(formData.dtAfastamento, formData.dtAvisoPrevio) < 0) {
        erros.push('Data de Afastamento deve ser posterior a data de Aviso Prévio.');
      }

      if (formData.avisoPrevioIndenizado && formData.jaRecebeuAvisoPrevioTrabalhado) {
        erros.push('O campo Já recebeu o Aviso Prévio Trabalhado não pode ser marcado para o Aviso Prévio Indenizado.');
      }

      if (formData.homolognet && EnumUtil.contains(SEM_COMPATIBILIDADE_HOMOLOGNET_ENUM_VALUES, formData.tipoDemissao)) {
        erros.push(
          'O campo Motivo não pode ser ' +
            EnumUtil.find(SEM_COMPATIBILIDADE_HOMOLOGNET_ENUM_VALUES, formData.tipoDemissao).label +
            ', quando a opção Gerar Homolognet estiver marcada.'
        );
      }
    }

    return erros;
  };

  const simularRescisaoClick = () => {
    const values = formProps.getValues();
    if (values.rescisaoJaCalculada) {
      promisedAlert('A rescisão já foi calculada.', AlertType.WARNING);
    } else if (values.naoCalcularRescisao) {
      promisedAlert('Para simular a rescisão a opção "Não Calcular Rescisão" precisa ser desmarcada.', AlertType.WARNING);
    } else {
      const erros = validateExtras(values, true);
      if (erros.length > 0) {
        promisedAlert(<ErroList erros={erros} />, AlertType.ERROR);
      } else {
        dispatch(showLoading());
        simularRescisao(
          { ...values, vinculo: fullVinculo },
          {
            thenFunction: res => {
              if (res && res.rescisaoDTO) {
                formProps.setValue(Object.keys(res.rescisaoDTO).map(key => ({ [key]: res.rescisaoDTO[key] })));
              }

              if (res && res.idArquivo) {
                fileDownload(res.idArquivo.split('<')[1], {
                  errorFunction: mensagem => {
                    dispatch(progressActions.hideProgress());
                    promisedAlert(mensagem, AlertType.ERROR);
                  },
                  thenFunction: response => {
                    dispatch(progressActions.hideProgress());
                    const filename = response.headers['content-disposition'].split('filename=')[1];
                    saveAs(response.data, filename);
                  },
                });
              }
            },
          }
        );
      }
    }
  };

  const apagarRescisaoClick = () => {
    const values = formProps.getValues();

    const resetRescisao = () => {
      formProps.setValue(
        Object.keys(values)
          .filter(key => key !== 'vinculo')
          .map(key => ({ [key]: null }))
      );
    };

    if (values.rescisaoJaCalculada) {
      promisedAlert(
        'A rescisão já foi calculada. Ao remover ela, seus cálculos também serão removidos. Essa operação é irreversível. ' +
          'Deseja realmente apagar a rescisão?'
      ).then(() => {
        apagarRescisao(values.id, {
          thenFunction: resetRescisao,
        });
      });
    } else {
      resetRescisao();
    }
  };

  const feriasDefaultSortProps: DefaultSortProps[] = [
    {
      dataField: 'dtInicialPerAquisitivo',
      order: OrderType.ASC,
    },
  ];

  const feriasFormProps: DetailManagerFormProps = {
    columns: [
      {
        dataField: 'dtInicialPerAquisitivo',
        text: 'Dt. Inicial do Período Aquisitivo',
      },
      {
        dataField: 'dtFinalPerAquisitivo',
        text: 'Dt. Final do Período Aquisitivo',
      },
      {
        dataField: 'nroFaltas',
        text: 'Nº de Faltas',
      },
      boolColumn({
        dataField: 'reflexoDobra',
        text: 'Reflexo de Dobra',
      }),
      {
        dataField: 'valor',
        formatter: decimalFormatter,
        text: 'Valor',
      },
    ],
    detailView: FeriasRescisaoEditView,
    formProps,
    propName: 'feriasRescisao',
    title: 'Férias a incluir',
  };

  const wizardFinishProps: WizardActionFinishProps = {
    confirmButton: {
      label: 'Salvar',
    },
    onCancel: () => {
      reset();
      onCancel();
    },
    onConfirm: () => {
      editFormRef.current.submit().then(reset);
    },
  };

  const getEditView = () => (
    <>
      <Row>
        <Col className="text-right">
          {condicional(
            isSimularVisivel,
            <Button color="first" onClick={simularRescisaoClick}>
              Simular Recisão
            </Button>
          )}
          <Button color="first" style={{ marginLeft: 10 }} onClick={apagarRescisaoClick}>
            Apagar Recisão
          </Button>
          <WikiButton pageId={643} />
        </Col>
      </Row>
      <Row>
        <Col>
          <Box title="Programação de Rescisão" className="mt-4">
            <Row>
              <Col>
                <Checkbox
                  id="naoCalcularRescisao"
                  formProps={formProps}
                  label="Programação de Rescisão"
                  tooltipProps={{
                    message:
                      "Caso seja selecionado a rescisão do vinculo não será calculada. Marque essa opção para gerar o evento 'S-2399 - Trabalhador Sem Vínculo de Emprego/Estatutário - Término' sem o grupo de verbas verbas rescisorias",
                  }}
                />
              </Col>
              <Col>
                <Checkbox
                  id="pagar13NaRescisao"
                  formProps={formProps}
                  label="Pagar 13º salário na rescisão"
                  tooltipProps={{
                    message: 'Caso DESMARQUE esta opção os valores referentes ao 13º salário serão calculados e emitidos nas guias de 13º salário',
                  }}
                />
              </Col>
              <Col md={5}>
                <Checkbox id="cumpriuJornadaSemanaAfastamento" formProps={formProps} label="Jornada de trabalho cumprida na semana do afastamento" />
              </Col>
            </Row>
            <Row style={{ marginTop: 15 }}>
              <Col>
                <Checkbox
                  id="dispensadoAvisoPrevio"
                  formProps={formProps}
                  label="Dispensa do Aviso Prévio"
                  onChange={changeAvisoDispensaIndenizado}
                />
              </Col>
              <Col>
                <Checkbox id="homolognet" formProps={formProps} label="Gerar Homolognet" />
              </Col>
              <Col md={5}>
                <Checkbox
                  id="arrecadouFgtsGuiaRecolhimentoRescisorio"
                  formProps={formProps}
                  label="FGTS arrecadado na Guia de Recolhimento Rescisório"
                />
              </Col>
            </Row>
            <Row style={{ marginTop: 15 }}>
              <Col>
                <Checkbox id="recolheFGTSMesAnterior" formProps={formProps} label="Recolher FGTS Mês Anterior" />
              </Col>
              <Col>
                <Checkbox id="jaRecebeuAvisoPrevioTrabalhado" formProps={formProps} label="Aviso Prévio Trabalhado Recebido" />
              </Col>
              <Col md={5}>
                <Checkbox id="empregoNoAvisoPrevio" formProps={formProps} label="Novo vínculo comprovado durante Aviso Prévio" />
              </Col>
            </Row>
            <Row style={{ marginTop: 15 }}>
              <Col>
                <Checkbox id="avisoPrevioIndenizado" formProps={formProps} label="Aviso Prévio Indenizado" onChange={changeAvisoDispensaIndenizado} />
              </Col>
            </Row>
            <Row style={{ marginTop: 25 }}>
              <Col>
                <InputGroupInline
                  id="dtAfastamento"
                  label="Data do Afastamento"
                  formProps={formProps}
                  isPrependLabel={true}
                  type={InputType.DATE}
                  onBlur={dataAfastamentoBlur}
                />
              </Col>
              <Col>
                <InputGroupInline id="dtAvisoPrevio" label="Data do Aviso Prévio" formProps={formProps} isPrependLabel={true} type={InputType.DATE} />
              </Col>
            </Row>
            <Row>
              <Col>
                <InputGroupInline
                  id="duracaoAviso"
                  label="Dias de Aviso Prévio"
                  formProps={{ ...formProps, validation: { ...onlyNumber(0, 3), max: 999 } }}
                  isPrependLabel={true}
                  type={InputType.NUMBER}
                />
              </Col>
              <Col>
                <InputGroupInline id="dtPagamento" label="Data do Pagamento" formProps={formProps} isPrependLabel={true} type={InputType.DATE} />
              </Col>
            </Row>
            <Row>
              <Col>
                <InputGroupInline id="vrPensao" label="Valor pensão" formProps={formProps} isPrependLabel={true} />
              </Col>
              <Col>
                <InputGroupInline id="percPensao" label="Percentual de Pensão" formProps={formProps} isPrependLabel={true} />
              </Col>
            </Row>
            <Row>
              <Col>
                <InputGroupInline id="percPensaoFGTS" label="Percentual de Pensão FGTS" formProps={formProps} isPrependLabel={true} />
              </Col>
              <Col>
                <InputGroupInline id="saldoFGTS" label="Saldo FGTS" formProps={formProps} isPrependLabel={true} />
              </Col>
            </Row>
            <Row>
              <Col>
                <InputGroupInline
                  id="cnpjEmpresaSucessora"
                  label="CNPJ Empresa Sucessora"
                  formProps={formProps}
                  isPrependLabel={true}
                  type={InputType.CNPJ}
                  disabled={mudancaRegimeJuridico}
                />
              </Col>
            </Row>
            <Row>
              <Col>
                <InputGroupInline
                  id="nrCertidaoObito"
                  label="Nr. Certidão de Óbito"
                  formProps={formProps}
                  isPrependLabel={true}
                  type={InputType.MASK}
                  inputMaskedProps={{ mask: '999999.99.99.9999.9.99999.999.9999999-99' }}
                  disabled={falecimentoEmpregado}
                />
              </Col>
            </Row>
            {condicional(
              exibirTipoAviso,
              <Row>
                <Col>
                  <InputGroupInline
                    id="tipoAvisoPrevio"
                    label="Tipo de Aviso Prévio"
                    formProps={formProps}
                    isPrependLabel={true}
                    placeholder="Selecione o Tipo de Aviso Prévio"
                    inputDropDownProps={{ optionsProvider: TIPO_AVISO_PREVIO_ENUM_VALUES }}
                    type={InputType.DROP_DOWN}
                  />
                </Col>
              </Row>
            )}
            <Row>
              <Col>
                <InputGroupInline
                  id="indicadorCumprimentoAvPrevio"
                  label="Cumprimento Aviso Prévio"
                  formProps={formProps}
                  isPrependLabel={true}
                  placeholder="Selecione o Cumprimento Aviso Prévio"
                  inputDropDownProps={{ optionsProvider: INDICADOR_CUMPRIMENTO_AV_PREVIO_ENUM_VALUES }}
                  type={InputType.DROP_DOWN}
                  disabled={disabledCumprimentoAviso}
                />
              </Col>
            </Row>
            <Row>
              <Col>
                <InputGroupInline
                  id="tipoDemissao"
                  label="Motivo"
                  formProps={formProps}
                  isPrependLabel={true}
                  placeholder="Selecione o Motivo"
                  inputDropDownProps={{ optionsProvider: DEMISSAO_ENUM_VALUES }}
                  type={InputType.DROP_DOWN}
                  onChange={motivoChange}
                  onBlur={motivoBlur}
                />
              </Col>
            </Row>
            <Row>
              <Col>
                <InputGroupInline
                  id="informacaoCaixa"
                  label="156 - Informações a CAIXA"
                  formProps={{ ...formProps, validation: { maxLength: 255 } }}
                  isPrependLabel={true}
                />
              </Col>
            </Row>
          </Box>
        </Col>
      </Row>
      <Row>
        <Col>
          <DetailManagerForm
            {...props}
            defaultSortDirection={OrderType.ASC}
            defaultSorted={feriasDefaultSortProps}
            {...feriasFormProps}
            keyField="rowIndex"
          />
        </Col>
      </Row>
    </>
  );

  return (
    <WindowComponent title="Manutenção de Rescisão" wrapBody={props.openViaDock === true} wikiId={2983}>
      <EditForm
        ref={editFormRef}
        formProps={formProps}
        editViewProps={props}
        dtoClassName={CLASS_NAME}
        externalSubmit={true}
        validateExtras={validateExtras}
      >
        {props.openViaDock === true ? (
          <div className="m-3">
            {getEditView()}
            <ProcessBar handleSubmit={handleSubmitViaDock} handleClose={onCancel} btnSubmitIcon="save" btnSubmitLabel="Salvar" />
          </div>
        ) : (
          <Wizard formProps={formProps} onAfterPageChange={onPageChange} finishProps={wizardFinishProps}>
            <WizardPage id="vinculo" title="Seleção do vínculo">
              <Row>
                <Col>
                  <VinculoIS id="vinculo" formProps={{ ...formProps, validation: { required: true } }} label="Vinculo" isPrependLabel={true} />
                </Col>
              </Row>
            </WizardPage>
            <WizardPage id="rescisao" title="Rescisão">
              {getEditView()}
            </WizardPage>
          </Wizard>
        )}
      </EditForm>
    </WindowComponent>
  );
};

export default RescisaoEditView;
